<template>
  <main id="main-content" class="container pt-5">
    <p class="text-primary text-center">Sistema de Reservas</p>
    <h3 class="text-center mb-5 text-uppercase fw-bolder fs-1">Olv√≠date del resto</h3>
    <p class="text-center">Registr√°ndote en Hostal Patagonia podr√°s reservar una habitaci√≥n para ti y tus acompa√±antes, y disfrutar de una estad√≠a inolvidable con servicios excepcionales y actividades emocionantes.</p>
    <div class="row d-flex justify-content-evenly mt-5">
      <div class="col-12 col-lg-5 border rounded-2 p-4 bg-body-tertiary">
        <h4 class="my-5 text-center"><i class="bi bi-check-circle-fill text-dark fs-2"></i><br/>Inicio de Sesi√≥n</h4>
      <form @submit.prevent="handleLogin" class="row g-3 text-center">
        <div class="col-12">
          <label for="email" class="form-label">Correo Electr√≥nico</label>
          <input type="email" v-model="datosLogin.email" class="form-control" id="email" placeholder="Ingresa tu e-mail" required>
        </div>
        <div class="col-12">
          <label for="password" class="form-label">Contrase√±a</label>
          <input type="password" v-model="datosLogin.password" class="form-control" id="password" placeholder="Ingresa tu contrase√±a" required>
        </div>
        <div class="col-12">
          <button type="submit" class="btn btn-primary">Iniciar Sesi√≥n</button>
        </div>
      </form>
      </div>
    <div class="col-12 col-lg-5 border rounded-2 p-4 bg-body-tertiary">
      <RegisterView/>
    </div>
  </div>
  </main>
</template>

<script>
import { mapActions } from 'vuex';
import RegisterView from './RegisterForm.vue';

export default {
  name: 'LoginView',
  components: {
    RegisterView,
  },
  data() {
    return {
      datosLogin: {
        email: '',
        password: ''
      }
    };
  },
  methods: {
    ...mapActions(['login']),
    async handleLogin() {
      try {
        const user = await this.login(this.datosLogin);
        // Comprobamos si es administrador
        if (user.adminStatus) {
          this.$router.push('/admin/usuarios');
        } else {
          this.$router.push('/mis-reservas');
        }
      } catch (error) {
        this.$swal('Error al iniciar sesi√≥n. Por favor, intenta de nuevo.');
        console.error('Error al iniciar sesi√≥n:', error);
      }
    }
  }
}
</script>

